@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  font-size: 12px;
  @apply font-medium text-gray-600;
}

@font-face {
  font-family: 'Inter UI';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('assets/fonts/Inter-UI-Regular.woff2') format('woff2'),
    url('assets/fonts/Inter-UI-Regular.woff') format('woff');
}

@font-face {
  font-family: 'Inter UI';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url('assets/fonts/Inter-UI-Medium.woff2') format('woff2'),
    url('assets/fonts/Inter-UI-Medium.woff') format('woff');
}

@font-face {
  font-family: 'Inter UI';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url('assets/fonts/Inter-UI-SemiBold.woff2') format('woff2'),
    url('assets/fonts/Inter-UI-SemiBold.woff') format('woff');
}

@font-face {
  font-family: 'Inter UI';
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url('assets/fonts/Inter-UI-ExtraBold.woff2') format('woff2'),
    url('assets/fonts/Inter-UI-ExtraBold.woff') format('woff');
}

.modal {
  max-width: calc(100vw - 32px);
  max-height: calc(100vh - 32px);
}

.editor ul {
  list-style-type: circle;
}
.editor ol {
  list-style-type: decimal;
}

#root,
body,
html {
  height: 100%;
}

.rap-container .rap-controls .rap-slider .rap-progress .rap-pin {
  right: -8px !important;
  top: -4px !important;
}

.rap-container .rap-slider .rap-progress .rap-pin {
  height: 12px !important;
  width: 12px !important;
}

.rap-container .rap-controls {
  font-size: 12px !important;
}

.rap-container {
  box-shadow: none !important;
  width: 100% !important;
  height: 40px !important;
}

.rap-container svg {
  width: 12px !important;
  height: 14px !important;
}

.rap-container .rap-pp-button {
  width: 12px !important;
}

.rap-container .rap-volume .rap-volume-btn {
  width: 12px !important;
}


h1.text-md.font-semibold {
  font-size: 23px;
  --tw-font-weight: 600;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: 0;
}

.bg-surface-base {
  background-color: #f8f9f5;
  color: #2e4320;
  width: 100%;
}

.eena {
  background-size: cover;
  background-position: center;
  background-image: url(https://assets.staticimg.com/ucenter-web/1.4.57/media/819a309d1eeed5d1a750.png);
  height: 100vh;
  width: 50%;
  flex-direction: column;
  display: flex;
}

.beena {
  height: 80vh;
  width: 50%;
  flex-direction: column;
  display: flex;
  justify-content: center;
  max-width: 28rem;
  margin-top: 2rem;
  margin-left: auto;
  margin-right: auto;
}

.kubox {
  box-sizing: border-box;
  min-height: 0px;
  min-width: 0px;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 31px;
}

.lrtcss-169t9ii {
  font-weight: 700;
  font-size: 40px;
  line-height: 130%;
  margin-top: 0px;
  word-break: break-word;
  color: rgb(29, 29, 29);
}

.ltrr {
  width: 100%;
  height: 48px;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  border-radius: 24px;
  font-size: 16px;
  color: rgb(29, 29, 29);
  font-feature-settings: "liga" 0, "clig" 0;
  border: 1px solid rgba(29, 29, 29, 0.12);
  padding: 0px 30px;
  cursor: pointer;
  overflow: hidden;
  font-weight: 500;
  margin-top: 32px;
}

img.lrtcss-t64tg4 {
